body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.link {
  color: #fff;
  width: fit-content;
  background-color: #db7093;
  border: none;
  border-radius: 5px;
  margin: 15px 0 50px 15px;
  padding: 10px 15px;
  font-size: 16px;
  text-decoration: none;
  display: block;
}

.link:hover {
  color: #000;
  background-color: #00f;
}

.conteiner {
  height: 100vh;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.search, .wrapper {
  flex-direction: column;
  display: flex;
}

.search-input, .search-button {
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 10px;
}

.search-input {
  width: 150px;
  border: 2px solid #00f;
  border-radius: 5px;
  padding: 7px 10px;
  font-size: 16px;
}

.search-button, .add {
  width: 150px;
  color: #fff;
  cursor: pointer;
  background-color: #00f;
  border: 2px solid #00f;
  border-radius: 5px;
  padding: 7px 10px;
  font-size: 16px;
}

.add:hover, .search-button:hover {
  background-color: tomato;
  border: 2px solid tomato;
}

.weather-list {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
  list-style: none;
  display: flex;
}

.weather-item {
  box-sizing: border-box;
  width: calc(33% - 30px);
  text-align: center;
  border: 2px solid #00f;
  border-radius: 15px;
  margin: 0 15px 15px 0;
  padding: 5px;
}

.weather-img {
  width: 100px;
}

